import { API } from "../../backend";

export const gethome=async()=>{
    return await fetch(`${API}/user/media/getIndexPageData`,{
        method:"GET",
        headers:{
            Accept:"application/json",
            "Content-Type":"application/json",
           
        },
        
    }).then((response)=>{
        console.log(response)
     return response.json()
    }).catch((e)=>{
      console.log(e)
    })
}