import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import {Button, CardActionArea, CardActions,Paper } from '@mui/material';
import AudioImage from "../Audio.png";
import ReactPlayer from 'react-player/lazy';
import ReactAudioPlayer from 'react-audio-player';
import Carousel from 'react-material-ui-carousel';


const CardData=({mediaName,src,description,clicked})=>{

  const card = (
    <React.Fragment>
      
    </React.Fragment>
  );


if(mediaName=="Image"){
  return (
    <Card>
      <CardActionArea >
        <img src={src} style={{padding:"15px",width:"100%"}}/>
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
         {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined" style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}} >More Images</Button>
      </CardActions>
    </Card>
  )}

if(mediaName=="Vedio"){
  return (
    <Card >
      <CardActionArea>
          
      <ReactPlayer style={{padding:"15px"}}
                         
                            url= {src}
                            playing={false}
                            controls={true}
                     
                            config={{ file: { 
                                attributes: {
                                  controlsList: 'nodownload'  //<- this is the important bit
                                }
                            }}}
                           

                         
                            
                        />
                       
    
                    
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
          {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>MORE Videos</Button>
      </CardActions>
    </Card>
  )}
if(mediaName=="Audio"){
  return (
    <Card>
      <CardActionArea>
  
      <img src={AudioImage} style={{padding:"15px",height:"320px",width:"100%"}}/>

   
      <ReactAudioPlayer
  src={src}
  controls
  controlsList="nodownload"
/>  
                                           
        <CardContent>
          <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
        {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
      <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>MORE AUDIOS</Button>
      </CardActions>
    </Card>
  )}

  if(mediaName=="content"){
    return (
      <Card>
        <CardActionArea>
       
                
      <ReactPlayer style={{padding:"15px"}}
                         
                         url= {`https://www.youtube.com/watch?v=pi1ljoJPXTU`}
                         playing={false}
                         controls={true}
                  
                         config={{ file: { 
                             attributes: {
                               controlsList: 'nodownload'  //<- this is the important bit
                             }
                         }}}   
                     />
                         <CardContent>
                         <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
                       {description}
                         </Typography>
                       </CardContent>
        </CardActionArea>
        <CardActions>
        <Button variant="outlined" onClick={clicked}  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>Learn more</Button>
        </CardActions>
      </Card>
    )}



    if(mediaName=="content1"){
      return (
        <Card>
          <CardActionArea>
           <h3 style={{color:'#E34607',lineHeight:"34px"}}>Content</h3>
                           <CardContent>
        
                           <Typography variant="body2" color="text.secondary" style={{textAlign:"justify"}}>
                       {description}
                         <br />
                         <br/>
                        
      <div className="wrappper" style={{display:"flex",overflowX:"auto",'&::-webkit-scrollbar': { width: 0, }}}>
      
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px" }}>{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}}>{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
         <Card variant="outlined" style={{minWidth:"110px",height:"110px",padding:"15px",margin:"2px"}} >{card}</Card>
  
 
      </div>
    




                           </Typography>
                         </CardContent>
          </CardActionArea>
          <CardActions>
          <Button variant="outlined"  style={{borderRadius:"24px",color:"#E34607",borderColor:"#E34607"}}>Load more</Button>
          </CardActions>
        </Card>
      )}






  }

  export default CardData;
