import { API } from "../../backend"
import {Button } from '@mui/material';
import { useEffect,useState } from 'react';
import Header from "./Header";
import Footer from "./Footer";
import Grid from '@mui/material/Grid';
import { gethome } from './getapi';
import CardData from './IndexComponent/CardItem';
import { Link } from "react-router-dom";

const Home=()=>{

  const[receive, getdata]=useState([])
  let abc;
  useEffect(()=>{
 
  function getallApi(){
    gethome().then(data=>{
      console.log(data)
      getdata(data);
    })
  }  
//   abc=gethome().then(data=>{
//   console.log(data)
//  })

 //getdata(abc);
  getallApi();
  },[])

  const share=async()=>{
   await navigator
    .share({
        url: window.location.href
   })
  }

    return(
      <>
     <Header/>
      <h2 style={{color:'#E34607',lineHeight:"34px",padding:"15px"}}>Today's sermon</h2>
      <center>
  
    
    <div className="row">   
     <div className="col-sm-6 d-flex mb-3">
     <CardData
                  mediaName="Image"
                  src={`${API}/user/media/resolvemedia?id=${receive[0]?.Image?.productPath}`}
                  description={receive[0]?.Image?.description}
                  /> 
     </div>
   
     <div className="col-sm-6 d-flex mb-3">
     <CardData
                  mediaName="Audio"
                  src={`${API}/user/media/resolvemedia?id=${receive[0]?.Audio?.productPath}`}
                  description={receive[0]?.Audio?.description}
                  /> 
     </div>
     </div>


     <div className="row">   
     <div className="col-sm-6 d-flex mb-3">
        
      
                        <CardData
                  mediaName="Vedio"
                  src={`${API}/user/media/resolvemedia?id=${receive[0]?.Vedio?.productPath}`}
                  description={receive[0]?.Vedio?.description}
                  />   
                 


              </div>
         
              <div className="col-sm-6 d-flex mb-3">
         <CardData
   mediaName="Vedio"
   src={`${API}/user/media/resolvemedia?id=${receive[0]?.Vedio?.productPath}`}
   description={receive[0]?.Vedio?.description}
   />   
  </div>

     </div>
  


      </center>
   
                     <center>
                      <br/>
                     <Grid container spacing={1}>
        <Grid item xs={6}>
      <Button onClick={share} variant="outlined" style={{borderRadius:"18px",color:"#E34607",backgroundColor:"#FFFBF4",borderColor:"White"}} >SHARE LINK</Button>          </Grid>
          <Grid item xs={6}>
         <Link to="/mainpage"> <Button variant="outlined" style={{borderRadius:"18px",color:"#E34607",borderColor:"#FFFBF4",backgroundColor:"#FFFBF4"}} >VISIT SITE</Button></Link>
          </Grid>
          </Grid>

                     
                      </center>   
                      <br />

                      <Footer />
         
        </>
    )
  

}

export default Home;