import { useEffect,useState } from "react";
import { getAllProducts } from ".";
import LoggedInNavbar from "./loggedInNavbar";
import { isAuthenticated } from "../../Auth/helper"
import { API } from "../../backend"
const ShowImage=(props)=>{
const [prdtdt,setprdt]=useState([]);


   useEffect(()=>{
  const fetchdt=()=>{
      try{
    let c=isAuthenticated();
    const {token,user}=c;
    console.log(token,user)
    let params = new URLSearchParams(document.location.search);
        let imgid = params.get("imgid");
     getAllProducts(user._id,token,imgid).then(x=>{
        console.log(x)

        setprdt(x)
    })

  
  }
  catch{}
}

  fetchdt()

   },[]) 


   const showData=()=>{
 console.log(prdtdt)
        return(<>
         {prdtdt.map((item,key)=>{
             console.log(item)
             return(<>
                   <figure className="figure">
  <img src={`${API}user/getproduct/${isAuthenticated().user._id}/${item._id}`} className="figure-img img-fluid rounded" alt="A generic square placeholder image with rounded corners in a figure." />
  <figcaption className="figure-caption">A caption for the above image.</figcaption>
</figure>
             
             </>)
         })}
   
        </>)


   }

    return(<>
      <div className="container">

      
      <center><h3 style={{color:"white"}}>Images</h3><hr />
      {showData()}
    

    </center>
    </div>
    </>)
}

export default ShowImage;